.leadership {
  position: relative;
  padding-top: 96px;
  padding-bottom: 96px;

  @media (max-width: 767.98px) {
    padding-top: 80px;
    padding-bottom: 80px;
  }

  .content-area {
    .title-area {
      margin-bottom: 64px;

      @media (max-width: 767.98px) {
        margin-bottom: 32px;
      }
    }
    .leadership-boxes {
      display: flex;
      flex-wrap: wrap;
      column-gap: 56px;
      row-gap: 32px;

      @media (max-width: 767.98px) {
        column-gap: 8px;
      }

      .leadership-box {
        flex: 0 0 calc((100% - 112px) / 3);
        max-width: calc((100% - 112px) / 3);
        position: relative;

        @media (max-width: 767.98px) {
          flex: 0 0 calc((100% - 8px) / 2);
          max-width: calc((100% - 8px) / 2);
        }

        .image-area {
          position: relative;
          width: 100%;
          padding-top: 97.4%;
          margin-bottom: 16px;

          img {
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            object-fit: cover;
            object-position: center;
          }
        }
        .text-area {
          display: flex;
          column-gap: 8px;
          row-gap: 8px;

          @media (max-width: 767.98px) {
            flex-wrap: wrap;
          }

          .name-title {
            @media (max-width: 767.98px) {
              flex: 0 0 100%;
              max-width: 100%;
            }
            h4 {
              color: #003a51;
              font-size: 26px;
              font-style: normal;
              font-weight: 700;
              line-height: 26px;
              margin-bottom: 0;
            }
            p {
              color: #282828;
              font-size: 16px;
              font-style: normal;
              font-weight: 400;
              line-height: 150%;
              margin-bottom: 0;
            }
          }
          .social {
            @media (max-width: 767.98px) {
              flex: 0 0 100%;
              max-width: 100%;
            }
            a {
              position: relative;
              z-index: 2;
              img {
                height: 22px;
              }
            }
          }
        }
      }
    }
  }
}
